<template>
  <div class="paginator" v-if="pageConfig.total">
    <p class="record-num">共 <span>{{pageConfig.total}}</span> 条记录</p>
    <a href="javascript:;" class="page-prev spr" @click="prevPage()"></a>
    <!--<a href="javascript:;" class="page on">1</a>-->
    <a href="javascript:;"
       class="page"
       v-for="pages in pageArr"
       :key="pages"
       :class="{on: pages === pageConfig.current}"
       @click="goPage(pages)"
       v-show="pageBtnNum<=7
               || (pageBtnNum>7&& pageConfig.current<6&& pages<=7)
               || (pageBtnNum>7&& (pageBtnNum-pageConfig.current)<6 && pages>(pageBtnNum-7))
               || (pageBtnNum>7&& pageConfig.current>=6 && (pageBtnNum-pageConfig.current)>= 6&& ((pageConfig.current+1-pages)>=-3 && (pageConfig.current+1-pages)<=3))"
    >{{pages}}</a>
    <a href="javascript:;" class="page-next spr" @click="nextPage()"></a>
  </div>
</template>

<script>
    export default {
      name: "MyPaginator",
      props: ["pageConfig","pageName"],
      data(){
        return{
          limit: 10,
        }
      },
      computed:{
        pageBtnNum:function () {
          return Math.ceil(this.pageConfig.total/this.limit)
        },
        pageArr(){
          //let pageNum = Math.ceil(this.pageConfig.total/this.limit);
          let arr = [];
          for( let i=1;i<this.pageBtnNum+1;i++){
            arr.push(i)
          };
          return arr
        }
      },
      methods:{
        goPage(current){
          this.$store.commit(`${this.pageName}PageCurrent`,current);
        },
        prevPage(){
          if(this.pageConfig.current > 1){
            this.goPage(this.pageConfig.current-1);
          }else{
            this.$onlyTxtPopShow(this.$errTxt.firstPage);
          }
        },
        nextPage(){
          if(((this.pageConfig.current+1)*this.limit - this.pageConfig.total) < 10){
            this.goPage(this.pageConfig.current+1);
          }else{
            this.$onlyTxtPopShow(this.$errTxt.lastPage);
          }
        }
      }
    }
</script>

<style scoped>

</style>



