<template>
  <div>
  <div id="commPop"
       class="pop"
       v-if="dialogName != 'none'"
       :class="pop[dialogName].className"
  >
    <p>{{pop[dialogName].title}}</p>

    <textarea
      v-if="dialogName === 'deleteAuthority'"
      name=""
      id=""
      v-model="comment"
      cols="30"
      rows="10"
      placeholder="请简单描述原因"
      class="tc-reasons-box"
    ></textarea>

    <a href="javascript:;"
       class="tc-reasons-submit"
       v-if="pop[dialogName].event"
       @click="pop[dialogName].event"
    >{{pop[dialogName].eventTxt}}</a>
    <a v-if="pop[dialogName].btn.includes('cancel')"
       class="tc-reasons-cancel"
       @click="$store.commit('changeDialog','none')"
    >取消</a>
    <a v-if="pop[dialogName].btn.includes('abnormity')"
       class="tc-abnormity-btn"
       @click="$store.commit('changeDialog','none')"
    >确定</a>
  </div>
  </div>
</template>

<script>
    import {mapState} from 'vuex';
    export default {
      name: "MyDialog",
      data(){
        return{
          comment: null,
          pop:{
            none:{
              title: '',
              btn: [],
              className: '',
              event:''
            },
            deleteAuthority:{
              title: '请填写权限变更原因',
              btn: ['cancel'],
              className: "tc-reasons",
              event: this.deleteAuthority,
              eventTxt: '提交处理'
            },
            onlyTxt:{
              title: this.popTxt,
              btn: ['abnormity'],
              className: "tc-abnormity",
            },
          }
        }
      },
      computed: {
        ...mapState(["dialogName","popTxt"]),
      },
      watch:{
        popTxt(newVal){
          this.pop.onlyTxt.title = newVal;
        },
      },
      mounted(){
        this.pop.onlyTxt.title = this.popTxt;
      },
      methods:{
        deleteAuthority(){
          this.$emit('deleteAuthority',this.comment);
        }
      }
    }
</script>

<style scoped>
  #commPop{
    z-index: 9999;
  }
</style>



